
import {Component,ViewChild,OnInit} from '@angular/core';
import {MatPaginator} from '@angular/material/paginator';
import {MatSort} from '@angular/material/sort';

import {MatTableDataSource} from '@angular/material/table';
import { Http } from '@angular/http';
import { map } from 'rxjs/operators';
import { Observable } from 'rxjs';
import {BatteriesService} from '../batteries.service'
import {MachinesService} from '../machines.service'
import {FormControl} from '@angular/forms';
import { BatteryCardsComponent } from '../battery-cards/battery-cards.component';


export interface Machine{
  OEM:String,
  catergory: String,
  model:String,
  voltage:number,
  minCap:number,
  maxCap:number, 
  energy:number,
  minWeight:number,
  width:number,
  len:number,
  height:number,
  verified:boolean
  
  
}


@Component({
  selector: 'app-machines',
  templateUrl: './machines.component.html',
  styleUrls: ['./machines.component.css']
})
export class MachinesComponent implements OnInit {

 
  @ViewChild(MatPaginator, {static: true}) paginator: MatPaginator;dataSource: MatTableDataSource<Element>;
  
  @ViewChild(MatSort, {static: true}) sort: MatSort;
  Batteries = new FormControl();
  BatteryNames=[];
  data: any = null;
  
  ShowAdd = false;
  Update = false;
  UpdateData: Machine[]=[];

 
  AddMachineData: Machine[] =[];



  constructor(public http: Http, public BS: BatteriesService, public MS: MachinesService) {
  this.getTableData()
  this.getBats()


    
   }
     ngOnInit() {
    this.getTableData()

  }

   applyFilter(event: Event) {
    var test = (event.target as HTMLInputElement).value
    const filterValue = test.toString();

    this.dataSource.filter = filterValue.trim().toLowerCase();

    if (this.dataSource.paginator) {
      this.dataSource.paginator.firstPage();
    }
  }
  
 

  displayedColumns: string[] = ['OEM', 'Catergory', 'Model', 'voltage','minCap','maxCap','Energy','minWeight','Width','Length','Height','verified','UpdateButtons', 'DelButtons'];
  
  addBattery()
  {
    console.log(this.AddMachineData);

    this.MS.addMachine(this.AddMachineData).subscribe(data => 
      {
        console.log(data);
        this.getTableData();
        for (var x in this.AddMachineData)
        {
          console.log( this.AddMachineData[x],x)
          this.AddMachineData[x] =null;
          
        }
 
        this.ShowAdd =!this.ShowAdd;
    
    
    
    });
      

  }
  DisplayUpdateData(element){
    this.Update = !this.Update;
    console.log(element)
    this.UpdateData = element;


  }
  deleteData(i)
  {
    // console.log(i);
    var machineId = this.dataSource[i]['_id']
    // console.log(this.dataSource[i]['_id'])
    this.MS.DelMachine(machineId).subscribe(data => 
      {
        // console.log(data);
        this.getTableData();  
    
    
    
    });

  }


  UpdateMachineData(updateData)
  {
    console.log(updateData);

    this.MS.UpdateMachine(updateData).subscribe(data=>{
  
      console.log(data)

      for (var x in this.UpdateMachineData)
      {
        console.log( this.UpdateMachineData[x],x)
        this.UpdateMachineData[x] =null;
       
      }

      this.Update = !this.Update;
    });

  }




  getTableData()
  {
    this.MS.getMachines() .subscribe((data) =>{ 
      var machineData = JSON.parse(data['_body'])
      this.dataSource =  new MatTableDataSource(machineData['machines'])
      this.dataSource.paginator = this.paginator;

      
        } );

      }

  getBats()
  {
    this.BS.getBatteries().subscribe((data) =>{ 
      var batteries = JSON.parse(data['_body'])
      var bats = batteries['batteries']
      for (var bat in bats){
        this.BatteryNames.push(bats[bat].name)
        // console.log(bats[bat].name)

      }
      // console.log(bats);
        } );
  }
}
